import React, { useEffect } from 'react';
import { PageHeader, Tabs, Input, Tree } from 'antd';

import CodeHighlight from 'code-highlight';
import AutoComplete from '../lib/ManiaFrameWork/js/AutoComplete';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'Data Entry',
  },
  {
    path: 'AutoComplete',
    breadcrumbName: 'AutoComplete',
  },
];
const { TreeNode, DirectoryTree } = Tree;

const AutoComplete_ = () => {
  useEffect(() => {
    var list = [
      " ", "", "M", "", "", "m",
      "forkakao", "AxE", "  ", "", "", "",
      "", "", "2 ", "", "", "",
      "2", "", "3", "", "", "[Android]", "[iOS]",
      "", "", "", "", "", " for kakao", "",
      "", "!", " ", "", "", "", "  ",
      "", "", "", " ", ""
    ];
    new AutoComplete(document.querySelector('#autocomplete'), list);
  }, []);


  return (
    <>
      <PageHeader title="AutoComplete" breadcrumb={{ routes }} subTitle="AutoComplete" style={{ padding: 10 }} />
      <div className="MF_area">
        <div className="autocomplete-wrapper">
          <Input placeholder="Search Games" id="autocomplete" />
        </div>
      </div>

      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<div class="autocomplete-wrapper">
  <input placeholder="Search Games" id="autocomplete" />
</div>`}
            </CodeHighlight>
          </TabPane>
          <TabPane tab="JAVASCRIPT" key="2">
            <CodeHighlight language="JS">
              {`var list = [
  " ", "", "M", "", "", "m",
  "forkakao", "AxE", "  ", "", "", "",
  "", "", "2 ", "", "", "",
  "2", "", "3", "", "", "[Android]", "[iOS]",
  "", "", "", "", "", " for kakao", "",
  "", "!", " ", "", "", "", "  ",
  "", "", "", " ", ""
];
new AutoComplete(document.querySelector('#autocomplete'), list);`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>
      <h3>Data List</h3>
      <DirectoryTree multiple defaultExpandAll >
        <TreeNode title="Lists" key="0-0">
          <TreeNode title=" " key="0-0-0" isLeaf />
          <TreeNode title="" key="0-0-1" isLeaf />
          <TreeNode title="M" key="0-0-2" isLeaf />
          <TreeNode title="" key="0-0-3" isLeaf />
          <TreeNode title="" key="0-0-4" isLeaf />
          <TreeNode title="m" key="0-0-5" isLeaf />
          <TreeNode title="AxE" key="0-0-6" isLeaf />
          <TreeNode title="  " key="0-0-7" isLeaf />
          <TreeNode title="" key="0-0-8" isLeaf />
          <TreeNode title="" key="0-0-9" isLeaf />
          <TreeNode title="" key="0-0-10" isLeaf />
          <TreeNode title="2" key="0-0-11" isLeaf />
          <TreeNode title="" key="0-0-12" isLeaf />
          <TreeNode title="3" key="0-0-13" isLeaf />
          <TreeNode title="" key="0-0-14" isLeaf />
          <TreeNode title="" key="0-0-15" isLeaf />
          <TreeNode title="[Android]" key="0-0-16" isLeaf />
          <TreeNode title="[iOS]" key="0-0-17" isLeaf />
          <TreeNode title="" key="0-0-18" isLeaf />
          <TreeNode title="" key="0-0-19" isLeaf />
          <TreeNode title="" key="0-0-20" isLeaf />
          <TreeNode title="" key="0-0-21" isLeaf />
          <TreeNode title="" key="0-0-22" isLeaf />
          <TreeNode title=" for kakao" key="0-0-23" isLeaf />
          <TreeNode title="" key="0-0-24" isLeaf />
          <TreeNode title="" key="0-0-25" isLeaf />
          <TreeNode title="!" key="0-0-26" isLeaf />
          <TreeNode title=" " key="0-0-27" isLeaf />
          <TreeNode title="" key="0-0-28" isLeaf />
          <TreeNode title="" key="0-0-29" isLeaf />
          <TreeNode title="" key="0-0-30" isLeaf />
          <TreeNode title="  " key="0-0-31" isLeaf />
          <TreeNode title="" key="0-0-32" isLeaf />
          <TreeNode title="" key="0-0-33" isLeaf />
          <TreeNode title="" key="0-0-34" isLeaf />
          <TreeNode title=" " key="0-0-35" isLeaf />
          <TreeNode title="" key="0-0-36" isLeaf />
        </TreeNode>
      </DirectoryTree>
    </>
  );
}

export default AutoComplete_;