import React from 'react';
import { PageHeader, Tabs } from 'antd';

import CodeHighlight from 'code-highlight';

// import all the styles
import "code-highlight/lib/style.css";
import "highlight.js/styles/github-gist.css";

const { TabPane } = Tabs;
const routes = [
  {
    path: '/',
    breadcrumbName: 'Data Display',
  },
  {
    path: 'Accordions',
    breadcrumbName: 'Accordions',
  },
];
const Accordions = () => {
  return (
    <>
      <PageHeader title="Accordions-FAQ" breadcrumb={{ routes }} subTitle="Accordions" style={{ padding: 10 }} />

      <div className="MF_area">
        <ul className="faq_contents_wrap">
          <li className="faq_title">FAQ</li>
          <li className="faq_content">
            <button>
              <div>(ID)?</div>
              <div>
                 PIN   ,    <br />
                 ID      .
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>   ?</div>
              <div>
                     1.
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>   ?</div>
              <div>
                 SMS          ID       
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>  ?</div>
              <div>
                []  [ ]  [(ID)]   .
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>   </div>
              <div>
                   1 ,         .<br />
                    <br />
                     ,  ,       .<br />
                   []  [ ]  [(ID)]   .<br />
                  3 ,  (02-561-0671)   .
              </div>
            </button>
          </li>
          <li className="faq_content">
            <button>
              <div>(ID)   </div>
              <div>
                 (02-561-0671)   .
              </div>
            </button>
          </li>
        </ul>
      </div>
      <div className="codeArea">
        <Tabs defaultActiveKey="1">
          <TabPane tab="HTML" key="1">
            <CodeHighlight language="HTML">
              {`<ul class="faq_contents_wrap">
  <li class="faq_title">FAQ</li>
  <li class="faq_content">
    <button>
      <div> <!-- Q --> </div>
      <div> <!-- A --> </div>
    </button>
  </li>
  <li class="faq_content">
    <button>
      <div> <!-- Q --> </div>
      <div> <!-- A --> </div>
    </button>
  </li>
  <li class="faq_content">
    <button>
      <div> <!-- Q --> </div>
      <div> <!-- A --> </div>
    </button>
  </li>
</ul>`}
            </CodeHighlight>
          </TabPane>
        </Tabs>
      </div>


    </>
  );
}

export default Accordions;