class AutoComplete {
  constructor(element, data) {
    this.element = element;
    this.data = data;
    this.filterData = [];
    this.checkChoSung = true;
    this.element.addEventListener('keyup', () => this.element.value.length ? this._dataFiltering() : this.element.parentNode.querySelector('ul').style.display = 'none');
  }
  _serchResultMake() {
    this.element.parentNode.querySelector("ul") && this.element.parentNode.removeChild(this.element.parentNode.querySelector("ul"));
    let searchResultUl = document.createElement('ul');
    this.filterData.forEach((item) => {
      let searchResultLi = document.createElement('li');
      let liInnerHtml = '';
      for (let i = 0; i < item.length; i++) {
        liInnerHtml += item[i] === this.element.value[i - item.indexOf(this.element.value)] ? `<span>${item[i]}</span>` : item[i];
      }
      searchResultLi.innerHTML = liInnerHtml;
      searchResultUl.appendChild(searchResultLi)
      for (var i = 0; i < searchResultLi.querySelectorAll("span").length; i++) searchResultLi.querySelectorAll("span")[i].style.color = "red"
    })
    this.element.parentNode.appendChild(searchResultUl);
  }
  _dataFiltering() {
    this._checkChoSung();
    this.filterData = this.data.filter(item => this._checkInArray(this._toKorChars(item), this._toKorChars(this.element.value))); //    
    if (this.filterData.length === 0) this.filterData = this.data.filter(item => item.indexOf(this.element.value) >= 0 && this.element.value.length >= 2 ? true : false); //  
    !this.element.value.length ? this.element.parentNode.querySelector('ul').style.display = 'none' : this._serchResultMake();
  }
  _checkInArray(array1, array2) {
    let checkArray = [];
    let checkResult = true;
    for (var i = 0; i < array2.length; i++) {
      checkArray.push(array1[i] === array2[i] ? true : false);
    }
    for (var j = 0; j < checkArray.length; j++) {
      if (!checkArray[j]) {
        checkResult = false;
        continue;
      }
    }
    return !checkResult ? false : true;
  }
  _checkChoSung() { //    
    let cCode, str = this.element.value;
    for (let j = 0; j < this.element.value.length; j++) {
      cCode = str.charCodeAt(j);
      if (cCode === 32) { continue; } //    
      if (cCode < 0xAC00 || cCode > 0xD7A3) { //   
        this.checkChoSung = false;
        continue;
      }
      this.checkChoSung = true;
    }
  }
  _toKorChars(string) {
    const cCho = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
    const cJung = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
    const cJong = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
    let cho, jung, jong, cCode, str = string;
    let cnt = str.length, chars = [], chos = [];
    for (let i = 0; i < cnt; i++) {
      cCode = str.charCodeAt(i);
      if (cCode === 32) { continue; } //    
      if (cCode < 0xAC00 || cCode > 0xD7A3) { //   
        chars.push(str.charAt(i));
        chos.push(str.charAt(i));
        continue;
      }
      cCode = str.charCodeAt(i) - 0xAC00;
      jong = cCode % 28; //  
      jung = ((cCode - jong) / 28) % 21; // 
      cho = (((cCode - jong) / 28) - jung) / 21; //  
      chars.push(cCho[cho], cJung[jung]);
      chos.push(cCho[cho]);
      if (cJong[jong] !== '') {
        chars.push(cJong[jong]);
      }
    }
    return this.checkChoSung ? chars : chos;
  }
}

export default AutoComplete;
